<template>
    <TableBlock
        :data='data'
        :dataKey='dataKey'
        :title='title'
        :order='order'
        :heading=heading
    />
</template>

<script>
import TableBlock from '@/components/TableBlock'
export default {
    name: 'StatsGroup',
    components: { TableBlock },
    props: ['data', 'dataKey', 'title', 'order'],
    computed: {

        heading() {
            let current = 0
            let prev = 0
            const heading = ['Грейд']

            this.data.forEach((el) => {
                current += el.count
                prev += el.previous
            })

            heading.push(`Текущая неделя (${current})`)
            if (prev) {
                heading.push(`Предыдущая неделя (${prev})`)
            }
            return heading
        },

    }
}
</script>

