<template>
    <BaseContainer>
        <StartInfo :stats-compared="statsCompared" :stats-count="statsCount" />
    </BaseContainer>
</template>

<script>
import sources from '@/sources'
import BaseContainer from '@/UI/BaseContainer'
import StartInfo from '@/components/StartInfo'
import formattedDate from '@/helpers/formattedDate'
import getDateOfWeek from '@/helpers/getDayOfWeek'

export default {
    name: 'StartWeek',
    props: ['projects', 'developers'],
    components: { BaseContainer, StartInfo },
    data() {
        return {
            sources: sources,
        }
    },
    computed: {
        statsCount() {
            const data = [
                ['Запросы', this.projects.current],
                ['Разработчики', this.developers.current],
            ]

            if (this.projects.previous) {
                data[0].push(this.projects.previous)
            }
            if (this.developers.previous) {
                data[1].push(this.developers.previous)
            }

            const heading = ['', 'Текущая неделя']

            if (this.projects.previous || this.developers.previous) {
                heading.push('Предыдущая неделя')
            }

            return {
                data,
                heading,
            }
        },
        statsCompared() {
            const dateMaxProjects = formattedDate(this.projects.max.date)
            const weekdayMaxProjects = getDateOfWeek(this.projects.max.date)
            const dateMinProjects = formattedDate(this.projects.min.date)
            const weekdayMinProjects = getDateOfWeek(this.projects.min.date)


            const dateMaxDevelopers = formattedDate(this.developers.max.date)
            const weekdayMaxDevelopers = getDateOfWeek(this.developers.max.date)
            const dateMinDevelopers = formattedDate(this.developers.min.date)
            const weekdayMinDevelopers = getDateOfWeek(this.developers.min.date)


            return {
                heading: ['', 'Максимум', 'Минимум'],
                data: [
                    [
                        'Запросы',
                        `${this.projects.max.count} (${dateMaxProjects} - ${weekdayMaxProjects})`,
                        `${this.projects.min.count} (${dateMinProjects} - ${weekdayMinProjects})`,
                    ],
                    [
                        'Разработчики',
                        `${this.developers.max.count} (${dateMaxDevelopers} - ${weekdayMaxDevelopers})`,
                        `${this.developers.min.count} (${dateMinDevelopers} - ${weekdayMinDevelopers})`,
                    ],
                ],
            }
        },
    },
}
</script>

<style module>
.subheading {
    text-align: center;
}
</style>
