const formattedDate = (string) => {
    const date = new Date(string);
    console.log(date)

    const month = date.getMonth().toLocaleString("ru", {minimumIntegerDigits: 2});
    const day = date.getDate().toLocaleString("ru", {minimumIntegerDigits: 2});
    const year = date.getFullYear()

    return `${day}.${+month + 1}.${year}`
}

export default formattedDate
